:root {
  font-family: "Rajdhani", sans-serif !important;
  margin: 0;
  #dark {
		--lighter: #CBD6F0;
		--accent: #6484cd;
		--background-primary: #0A0D14;
		--background-secondary: #141A29;
		--background-secondary-alt: #141A29;
		--foreground-primary: #a8b5c3;	
		--purple: #492dd5ec;
	}
	#light {
		--lighter: #0A0D14;
		--accent: #1f3eb8;
		--background-primary: #f3f7ff;
		--background-secondary: #CBD6F0;
		--background-secondary-alt: #CBD6F0;
		--foreground-primary: #141A29;
		--purple: #492dd5ec;
	}
}

body {
  background-color: var(--background-primary);
  color: var(--accent);
  transition: background-color 1s ease-in-out;
  width: 100vw;
  margin: 0;
}

.container {
  position: relative;
  top: 0;
  left: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  margin: 0 auto;
  padding: 30px;
  background-color: var(--background-primary);
  color: var(--accent);
  transition: background-color 1s ease-in-out;
}

.setup-container {
  top: 10%;
  position: relative;
  width: 90vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 0.5em;
  padding-top: 30px;
  padding-bottom: 30px;
}

.form-container {
  position: relative;
  left: 0;
  right: 0;
  width: 80vw;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 0.5em;
}
@media (max-width: 767px) {
  .form-container {
    width: 80vw;
  }
  .codesection {
    width: 70vw;
  }
}

@media (min-width: 768px) {
  .form-container {
    width: 40vw;
  }
  .codesection {
    width: 30vw;
  }
}


.bar {
  top: 0;
  width: 100%;
  padding: 5px;
  background-color: var(--background-secondary);
  transition: background-color 1s ease-in-out;
  border: 1px var(--accent) solid;
  border-radius: 0.3em;
  display: flex;
}
.right {
  width: 20%;
  position: relative;
  display: flex;
  flex-direction: row;
  text-align: right;
  margin-left: auto;
  margin-right: 1px;
  right: 0;
  gap: 1em;
}
.gheader {
  font-size: 16px;
  font-weight: 800;
}
.right {
  align-items: right;
  justify-items: right;
}
.dateform {
  width: 100%;
}
.dateinput {
  padding: 2px;
  font-size: 16px;
  background-color: var(--background-primary) !important;
  color: var(--text-color-light);
}

.result-container {
  position: relative;
  top: 1%;
  right: 0;
  width: auto;
  height: auto;
  border-radius: 0.5em;
  margin-top: 10px;
  padding: 10px 10px;
  border: 1px var(--accent) solid;
  display: flex;
  flex-direction: column;
  align-items: center;
}





.saveform {
  position: relative;
  width: 50vw;
  margin-top: 2%;
  margin-bottom: 2%;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: left;
}
.buttonbar {
  position: relative;
  top: 0.5em;
  width: 80vw;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.listbuttonbar {
  margin: 5px;
  width: 90%;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

button {
  border-radius: 0.2em;
  font-size: 16px;
  width: auto;
  cursor: pointer;
}

.sbutton {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 0.7em;
  padding: 0.4em;
  width: 8em;
  border-radius: 0.3em;
  font-size: 16px;
  cursor: pointer;
  color: var(--lighter);
  background-color: var(--background-secondary);
  border: 0.15em solid var(--accent);
  
}

.tinput {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 2px solid var(--accent);
  background-color: var(--background-primary) !important;
  color: var(--text-color-light);
  border-radius: 0.2rem;
}

.list-container {
  position: relative;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.codesection {
  position: relative;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.codebuttons {
  display: flex;
  flex-direction: row;
}

.savedlist {
  background-color: var(--background-primary);
  width: 80vw;
  border-radius: 0.5em;
  padding: 3px 20px;
  /* border: 1px solid; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.savedtable {
  width: 60vw;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid var(--accent);
  border-radius: 0.5em;
  margin-bottom: 20px;
}
table {
  margin: 20px;
  padding: 20px;
}
th {
  text-align: left;
  width: 20vw;
}
tr {
  cursor: pointer;
}

.mainrow {
  border: 2px solid var(--accent);
  border-radius: 0.5rem;
}

.expanded-row {
  background-color: var(--background-secondary);
  width: 100%;
  height: 40px;
  text-align: center;
}